<template>
    <table class="table">
        <tbody>
            <tr v-for="(problem, index) in problems" v-bind:key="index">
                <td>
                    <router-link :to="{ name: 'problem', params: { id: problem.id }}">{{ problem.title }}</router-link>
                </td>
                <td>
                    <span>{{ problem.created_at }}</span>
                </td>
            </tr>
        </tbody>
    </table>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            problems: []
        }
    },
    created() {
        axios.get("/problems").then((data) => {
            this.problems = data.data;
        }).catch(err => console.error(err));
    }
}
</script>